import { _decorator, Component, Node, resources, Prefab, instantiate, Layers, find } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('createFengManeger')
export class createFengManeger extends Component {
    currentBlockNum: number;
    start() {
        this.node.on(Node.EventType.MOUSE_DOWN,this.addFeng,this)
    }

    update(deltaTime: number) {
        
    }

    addFeng(){
        this.createFeng()
    }

    createFeng(){
        let that = this;
        resources.load("perfabs/FengYiPer",Prefab,(err,Prefab)=>{
            if(!Prefab){
                // console.log('子弹预制体为空')
            }else{
                let node = instantiate(Prefab);
                node.layer = Layers.Enum.UI_2D;
                node.setPosition(0,0,0);
                node.parent = find("Canvas/FengBox");
            }
        })
    }
}


